/* eslint-disable camelcase */
import React from 'react'
import {Card, Image} from 'semantic-ui-react'
import Img from 'gatsby-image'
import {Link} from 'gatsby'

const mapProductsToItems = products =>
products.map((product, index) => {
  return {
    as: Link,
    to: "./product/" + product.context.id,
    childKey: product.context.id,
    image: (
      <Image src={require("../../images/" + product.context.image)} alt={product.context.name}/>
    ),
    header: product.context.name,
    meta: <Card.Meta style={{color: 'dimgray'}}>{product.context.price}</Card.Meta>,
  }
})

export default ({products}) => (
  <Card.Group items={mapProductsToItems(products)} itemsPerRow={2} stackable />
)
